
import Search from 'vux/src/components/search/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Card from 'vux/src/components/card/index.vue'

export default {
components: {
Search,
Group,
Selector,
Divider,
Card
},
data() {
return {
val: "",
posts: [],
type: "id",
options: ["id", "title", "author", "type"]
};
},
watch: {
val: function() {
this.posts =
this.val && this.$store.getters.getPosts(this.type, this.val);
},
type: function() {
this.posts = [];
}
}
};
